


.gpt3__navbar {
    backdrop-filter: blur(5px);
    background: rgba(2, 0, 27, 0.945);
     background: linear-gradient(90deg, rgba(4, 1, 53, 0.486) 0%, rgba(7, 51, 87, 0.678) 46%, rgba(7, 51, 87, 0.507) 100%);
     position: fixed;
     width: 100%;
     height: 10%;
    box-shadow: rgba(0, 0, 0, 0.219) 0px 5px 15px;
    z-index: 99;
}

.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}


.logo-img{
    width: 72.56px;
    height: 26.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 15px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}


.un {
    display: inline-block;
}

.un:after {
    content: '';
    width: 0px;
    height: 2px;
    display: block;
    background: var(--gradient-text);
    transition: 400ms;
    border-top-right-radius: 20px;

}

.un:hover:after {
    width: 100%;
}

.un:hover {
    color: rgb(165, 121, 170);
}

.gpt3__navbar-sign p:hover {
    color:#FF4820;
}

.gpt3__navbar-sign p {
    transition: 0.3s ease-in-out;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    padding: 0.3rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 15px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
}

.gpt3__navbar-sign button:hover {
    background: rgb(231, 230, 230);
    color: #FF4820;
}


.gpt3__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
    text-align: center;


    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: -70px;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }

    .gpt3__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__navbar{
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}